import { _decorator, Component, Input, Label, Node, Sprite, Vec3 } from 'cc';
import { TweenTool } from '../../Tools/TweenTools';
import { Global } from '../../Global';
import { ResMgr } from '../../Mgr/ResMgr';
const { ccclass, property } = _decorator;

@ccclass('Cat')
/**
 * 
 */
export class Cat extends Component {

    //
    @property(Node)
    private LeftHand: Node | null = null;

    //
    @property(Node)
    private RightHand: Node | null = null;

    //
    @property(Node)
    public ThinkBox: Node | null = null;

    //
    @property(Node)
    private Food: Node | null = null;

    //
    public FoodArray: string[] = [];

    //
    private CatPos: Vec3 = new Vec3(-217.798, 171.458, 0);

    //
    public MenuName: string = "";

    //
    public CatBreed: string = "";

    protected onLoad(): void {
        this.CatPos = this.node.getPosition();
        console.log(this.CatPos)
    }

    /**
     * 
     */
    public AppearCat() {
        this.node.active = true;
        this.node.setPosition(this.CatPos);
        TweenTool.CatAppear(this.node, () => {
            this.ThinkBox.active = true;
        });
    }

    /**
     * 
     * @param 
     */
    public UpdateInfor(_breed: string) {
        this.CatBreed = _breed;
        let cat: string = Global.CatBreed[_breed].sprite;
        this.node.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames[cat];
        this.LeftHand.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames[cat + "hand"];
        this.RightHand.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames[cat + "hand"];
    }

    /**
     * 
     * @param 
     */
    public UpdateFood(_menuname: string) {
        this.MenuName = _menuname;
        this.Food.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames[_menuname];
        this.FoodArray = Global.Menu[_menuname]
    }

    /**
     * 
     * @param 
     * @param 
     */
    public DisplayCat(_menuname: string, _breed: string) {
        this.ThinkBox.active = false;
        TweenTool.CatDisplay(this.node, () => {
            this.UpdateFood(_menuname);
            this.UpdateInfor(_breed);
            this.scheduleOnce(this.AppearCat, 0.1);
        });
    }

}


