import { _decorator, Component, Label, Node, tween, UIOpacity } from 'cc';
import { TopScene } from './TopScene';
const { ccclass, property } = _decorator;

@ccclass('PopBox')
export class PopBox extends Component {

    //
    @property(Label)
    private Content: Label | null = null;

    //
    @property(UIOpacity)
    private Uiopacity: UIOpacity | null = null;

    /**
     * 
     * @param 
     */
    public ShowToast(_content: string) {
        this.node.active = true;
        this.Content.string = _content;
        tween(this.Uiopacity).set({ opacity: 0 }).to(0.4, { opacity: 255 }).delay(1).to(0.2, { opacity: 0 }).call(() => {
            this.node.parent.getComponent(TopScene).HideAll();
        }).start();
    }

}


