import { _decorator, Component, EventTouch, Input, instantiate, Node, Prefab, UITransform, Vec3 } from 'cc';
import { MessageCenter } from '../../Message/MessageCenter';
import { MessageType } from '../../Message/Message';
import { Global } from '../../Global';
import { SingleMenu } from './SingleMenu';
import { LocalMgr } from '../../Mgr/LocalMgr';
import { AudioMgr } from '../../Mgr/AudioMgr';
import { AdMgr } from '../../Mgr/AdMgr';
const { ccclass, property } = _decorator;

@ccclass('PopMenu')
/**
 * 
 */
export class PopMenu extends Component {

    //
    @property(Prefab)
    private SingleMenu: Prefab | null = null;

    //
    @property(Node)
    private MainLoad: Node | null = null;

    //
    @property(Node)
    private ContentLoad: Node | null = null;

    protected start(): void {
    }

    /**
     * 
     * @param 
     */
    public AddSingle(_menu: string) {
        let singlemenu: Node = instantiate(this.SingleMenu);
        this.MainLoad.addChild(singlemenu);
        singlemenu.getComponent(SingleMenu).UpdateContent(_menu, Global.Menu[_menu][0], Global.Menu[_menu][1], Global.Menu[_menu][2])
    }

    protected onEnable(): void {

        //
        if (Global.ChangeMenu) {
            Global.ChangeMenu = false;
            this.MainLoad.removeAllChildren();

            for (let key of LocalMgr.instance.LocalInfor.menu) {
                this.AddSingle(key);
            }

        }

        this.MainLoad.setPosition(new Vec3(0, 250, 0))
        this.ContentLoad.on(Input.EventType.TOUCH_MOVE, this.TouchMove, this);

        return;
    }

    protected onDisable(): void {
        this.ContentLoad.off(Input.EventType.TOUCH_MOVE, this.TouchMove, this);
    }

    /**
     * 
     */
    private TouchMove(_e: EventTouch) {

        if (this.MainLoad.getComponent(UITransform).height <= 480) return;

        let pos_y: number = this.MainLoad.getPosition().y + _e.getDeltaY();

        if (pos_y <= 250) pos_y = 240;
        if (pos_y >= this.MainLoad.getComponent(UITransform).height - 250) pos_y = this.MainLoad.getComponent(UITransform).height - 250

        this.MainLoad.setPosition(new Vec3(0, pos_y, 0))

    }


    /**
     * 
     */
    private CloseClick() {
        AudioMgr.instance.PlayButton();
        MessageCenter.SendMessage(
            MessageType.Type_Top,
            MessageType.Top_HideAll,
            null
        )
    }
}


