import { _decorator, Animation, Component, Label, Node } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('ChooseScene')
/**
 * 
 */
export class ChooseScene extends Component {

    //
    private FoodNum = {
        fish: 0,
        chicken: 0,
        beef: 0,
        egg: 0,
        grass: 0
    }

    /**
     * 
     */
    public Init() {
        for (let key in this.FoodNum) {
            this.FoodNum[key] = 0;
            this.node.getChildByName(key + "").getChildByName("Num").getComponent(Label).string = "0";
        }
    }

    /**
     * 
     * @param 
     */
    public UpdateFood(_foodname: string) {
        this.FoodNum[_foodname]++;
        this.node.getChildByName(_foodname).getChildByName("Num").getComponent(Label).string = this.FoodNum[_foodname];
        this.node.getChildByName(_foodname).getChildByName("Food").getComponent(Animation).play();
    }

}


