import { _decorator, bezierByTime, Component, EventTouch, Input, instantiate, Node, Prefab, UITransform, Vec3, view, Widget } from 'cc';
import { BaseLoad } from '../../Message/BaseLoad';
import { BuildManager } from '../../Message/Manager/BuildManager';
import { Message, MessageType } from '../../Message/Message';
import { Global } from '../../Global';
import { TweenTool } from '../../Tools/TweenTools';
import { MessageCenter } from '../../Message/MessageCenter';
import { AudioMgr } from '../../Mgr/AudioMgr';
import { SingleBuild } from './SingleBuild';
import { LocalMgr } from '../../Mgr/LocalMgr';
const { ccclass, property } = _decorator;

@ccclass('BuildScene')
export class BuildScene extends BaseLoad {

    //
    private IsClick: boolean = false;

    //
    @property(Node)
    private MainLoad: Node | null = null;

    //
    @property(Node)
    private BigJia: Node | null = null;

    //
    @property(Prefab)
    private SingleBuild: Prefab | null = null;

    //
    private BuildArray: Node[] = [];

    protected onLoad(): void {
        BuildManager._instance.RegisterReceiver(this);

        for (let i = 0; i < LocalMgr.instance.LocalInfor.toyinfor.length; i++) {
            this.AddBuild(LocalMgr.instance.LocalInfor.toyinfor[i]);
        }

        this.node.active = false;
    }

    /**
     * 
     */
    ReceiveMessage(_msg: Message): void {
        Global.IsDebug && console.log("BuildScene", _msg);
        switch (_msg.Command) {
            case MessageType.Build_Init:
                this.Init();
                break;
            case MessageType.Build_Buy:
                this.BuyToy(_msg.Content.buildindex, _msg.Content.toyinfor);
                break;
            case MessageType.Build_AddBuild:
                LocalMgr.instance.AddBuild()
                this.AddBuild({ CatPic: "", CatHome: "", CatBall: "", CatBowl: "", CatWallShi: "" });
            default:
                break;
        }
    }

    protected onEnable(): void {
        this.MainLoad.getComponent(Widget).bottom = 180;
        this.node.on(Input.EventType.TOUCH_MOVE, this.TouchMove, this);
    }

    protected onDisable(): void {
        this.node.off(Input.EventType.TOUCH_MOVE, this.TouchMove, this);
    }

    /**
     * 
     */
    private TouchMove(_e: EventTouch) {
        if (this.MainLoad.getComponent(UITransform).height <= screen.height * 2) return;

        let bottom_num: number = this.MainLoad.getComponent(Widget).bottom + _e.getDeltaY();

        if (bottom_num >= 180) bottom_num = 180;
        else if (bottom_num <= screen.height * 2 - this.MainLoad.getComponent(UITransform).height) bottom_num = screen.height * 2 - this.MainLoad.getComponent(UITransform).height

        this.MainLoad.getComponent(Widget).bottom = bottom_num;
    }

    /**
     * 
     */
    private Init() {
        this.IsClick = false;
        this.node.active = true;
        TweenTool.SceneMove(this.node, new Vec3(-750, 0, 0), new Vec3(0, 0, 0), () => {
            this.IsClick = true;
            if (!LocalMgr.instance.GetFirst("build")) {
                MessageCenter.SendMessage(
                    MessageType.Type_Top,
                    MessageType.Top_ShowInstru,
                    "rule2"
                );
                LocalMgr.instance.UpdateFirst("build");
            }
        });
    }

    /**
     * 
     * @param 
     * @param 
     */
    private BuyToy(_buildindex: number, _toyinfor: { type: string, sprite: string }) {
        this.BuildArray[_buildindex].getComponent(SingleBuild).UdpateToy(_toyinfor.type, _toyinfor.sprite)
    }

    /**
     * 
     */
    private BigjiaClick() {
        AudioMgr.instance.PlayButton();

        LocalMgr.instance.AddBuild()
        this.AddBuild({ CatPic: "", CatHome: "", CatBall: "", CatBowl: "", CatWallShi: "" });
    }

    /**
     * 
     * @param 
     */
    private AddBuild(_buildinfor: { CatPic: string, CatHome: string, CatBall: string, CatBowl: string, CatWallShi: string }) {
        let singlebuild: Node = instantiate(this.SingleBuild);
        this.MainLoad.addChild(singlebuild);
        singlebuild.getComponent(SingleBuild).UdpateContent(_buildinfor, this.BuildArray.length);
        singlebuild.getComponent(SingleBuild).UpdateCat(LocalMgr.instance.LocalInfor.cat[this.BuildArray.length]);
        this.BuildArray.push(singlebuild);
        // this.BigJia.setSiblingIndex(this.MainLoad.children.length - 1)
    }

}


