import { _decorator, Animation, Component, Label, Node, Sprite } from 'cc';
import { Global } from '../../Global';
import { ResMgr } from '../../Mgr/ResMgr';
import { Cat } from './Cat';
import { CatLoad } from './CatLoad';
import { MessageType } from '../../Message/Message';
import { MessageCenter } from '../../Message/MessageCenter';
import { LocalMgr } from '../../Mgr/LocalMgr';
import { LoginScene } from './LoginScene';
import { AudioMgr } from '../../Mgr/AudioMgr';
const { ccclass, property } = _decorator;

@ccclass('DrawLoad')
/**
 * 
 */
export class DrawLoad extends Component {

    //
    @property(Animation)
    private BallAni: Animation | null = null;

    //
    @property(Animation)
    private GanAni: Animation | null = null;

    //
    @property(Node)
    private DownLoad: Node | null = null;

    //
    @property(Node)
    private FoodLoad: Node | null = null;

    //
    private FoodArray: string[] = [null, null, null];

    //
    @property(Node)
    private CatLoad: Node | null = null;

    //
    @property(Node)
    private Cookie: Node | null = null;


    /**
     * 
     */
    private GanClick() {

        for (let content of this.FoodArray) {//
            if (content == null) {
                this.SendPop("~")
                this.node.getComponent(Animation).play();
                return;
            }
        }

        this.BallAni.play();
        this.GanAni.play();

        this.FoodArray = this.FoodArray.sort();

        let check_cat: Node = null;
        for (let i = 0; i < 3; i++) {
            let cat: Node = this.CatLoad.getChildByName("Cat" + i.toString());
            let catfood: string[] = cat.getComponent(Cat).FoodArray.sort();

            if (this.FoodArray.toString() == catfood.toString()) check_cat = cat;
        }

        //
        let is_score: boolean = false;

        //
        if (check_cat != null) {
            AudioMgr.instance.PlaySuccess();
            //
            LocalMgr.instance.AddCat(check_cat.getComponent(Cat).CatBreed)
            //
            let coin_num: number = 1 + Math.floor(Math.random() * 4);
            this.CatLoad.getComponent(CatLoad).AddCoin(coin_num, check_cat.getPosition().x);
            LocalMgr.instance.AddCoin(coin_num);
            this.node.parent.getComponent(LoginScene).UpdateCoin();

        } else {
            AudioMgr.instance.PlayFail();
            //
            LocalMgr.instance.AddFail(1);
            check_cat = this.CatLoad.getChildByName("Cat0");
            this.SendPop("~~")
            this.node.getComponent(Animation).play();
        }

        this.CatLoad.getComponent(CatLoad).CatUpdateFood(check_cat);
        this.Cookie.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames[check_cat.getComponent(Cat).MenuName];
        this.Cookie.getComponent(Animation).play();

        setTimeout(() => {
            this.InitCookie();
        }, 1000);


        //
        for (let _index in this.FoodArray) {
            let box: Node = this.DownLoad.getChildByName("greenbox" + _index);
            let food: Node = box.getChildByName("egg");
            food.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames["jiahao"];
            this.FoodArray[_index] = null;
        }

        LocalMgr.instance.UpdateData();
    }

    /**
    * 
    * @param 
    */
    public UpdateBox(_food: string) {
        let box: Node = this.FoodLoad.getChildByName(_food + "Box");
        box.getChildByName("Num").getComponent(Label).string = LocalMgr.instance.LocalInfor.food[_food];
        box.getChildByName("Point").active = LocalMgr.instance.LocalInfor.food[_food] != 0;
    }

    /**
     * 
     * @param 
     */
    public AddFood(_e, _food: string) {
        AudioMgr.instance.PlayButton();
        if (LocalMgr.instance.LocalInfor.food[_food] == 0) {//
            this.SendPop("~~")
            return;
        }

        let changeindex: number = -1;

        //
        for (let i = 0; i < this.FoodArray.length; i++) {

            let box: Node = this.DownLoad.getChildByName("greenbox" + i.toString());
            let food: Node = box.getChildByName("egg");

            if (food.getComponent(Sprite).spriteFrame == ResMgr.instance.SpriteFrames["jiahao"] && changeindex == -1) {
                food.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames[_food];
                food.getComponent(Animation).play();
                changeindex = i;
            }
        }

        if (changeindex == -1) {
            Global.IsDebug && console.log("");
            return;
        }

        this.FoodArray[changeindex] = _food;
        LocalMgr.instance.AddFood(_food, -1)
        this.UpdateBox(_food);

    }

    /**
     * 
     */
    private GreenClick(_e, _clicknum: string) {
        AudioMgr.instance.PlayButton();
        let box: Node = this.DownLoad.getChildByName("greenbox" + _clicknum);
        let food: Node = box.getChildByName("egg");

        if (food.getComponent(Sprite).spriteFrame != ResMgr.instance.SpriteFrames["jiahao"]) {
            food.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames["jiahao"];
            LocalMgr.instance.LocalInfor.food[this.FoodArray[Number(_clicknum)]]++;
            this.UpdateBox(this.FoodArray[Number(_clicknum)]);
            this.FoodArray[Number(_clicknum)] = null;
        }
    }

    /**
     * 
     * @param 
     */
    private SendPop(_content: string) {
        MessageCenter.SendMessage(
            MessageType.Type_Top,
            MessageType.Top_ShowPopbox,
            _content
        );
    }

    /**
     * 
     */
    public InitCookie() {
        this.Cookie.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames["jinzhi"];
    }

}


