declare var wx;
declare var tt;
import { _decorator, Component, Label, Node, UITransform, Vec3 } from 'cc';
import { AssetList, Global } from '../Global';
import { ResMgr } from '../Mgr/ResMgr';
import { Main } from '../Main';
import { LocalMgr } from '../Mgr/LocalMgr';
import { AdMgr } from '../Mgr/AdMgr';
import { AudioMgr } from '../Mgr/AudioMgr';
import { Tools } from '../Tools/Tools';
import { WxTools } from '../Tools/WxTools';
import { DyTools } from '../Tools/DyTools';
const { ccclass, property } = _decorator;

@ccclass('Loading')
/**
 * 
 */
export class Loading extends Component {

    //
    @property(Node)
    private ProgressBar: Node | null = null;

    //
    private BarWidth: number = 0;

    //
    @property(Label)
    private Text: Label | null = null;

    start() {

        Global.IsWx = (typeof wx != "undefined");
        Global.IsDy = (typeof tt != "undefined");
        Global.IsDebug && console.log("", Global.IsWx, Global.IsDy);

        AdMgr.instance.Init();
        WxTools.ShareMenu();
        WxTools.SetShare()
        DyTools.CanShareFriend();

        this.BarWidth = this.ProgressBar.getComponent(UITransform).width;
        this.ProgressBar.setPosition(new Vec3(-this.BarWidth, 0, 0))

        this.GetLocalStroage();
        ResMgr.instance.LoadInit();
        // AdMgr.instance.Init();
        this.LoadRes();
    }

    /**
     * 
     */
    private async LoadRes() {
        await ResMgr.instance.LoadBundle(Global.ResName, 0.1);
        await ResMgr.instance.LoadRes(Global.ResName, AssetList.SpriteFrame, 0.7);
        await ResMgr.instance.LoadRes(Global.ResName, AssetList.Sound, 0.1);
        await ResMgr.instance.LoadRes(Global.ResName, AssetList.Prefabs, 0.1);
        AudioMgr.instance.Init();
        AudioMgr.instance.PlayBgSound("bg");
        //
        Main.instance.UpdateScene("GameScene")

    }

    /**
     * 
     */
    private GetLocalStroage() {

        LocalMgr.instance.LocalInfor = LocalMgr.instance.GetData();

        if (!LocalMgr.instance.LocalInfor) {
            LocalMgr.instance.LocalInfor = LocalMgr.instance.AllInfor;
            LocalMgr.instance.LocalInfor.gamestart = new Date().getTime();
        } else {
            let is_sameday: boolean = Tools.checkSameDay(new Date(), new Date(LocalMgr.instance.LocalInfor.gamestart));
            let add_heart: number = 3;
            if (!is_sameday) {
                LocalMgr.instance.LocalInfor.gamestart = new Date().getTime();
                LocalMgr.instance.AddHeart(add_heart);
            }
        }
        LocalMgr.instance.UpdateData();
    }

    update(deltaTime: number) {
        this.ProgressBar.setPosition(new Vec3(-this.BarWidth + this.BarWidth * Global.LoadProgress, 0, 0))
        this.Text.string = Math.floor(Global.LoadProgress * 100).toString() + "%";
        // this.Text.string = Math.floor(Global.LoadProgress * 100).toString();
    }


    protected onDestroy(): void {

    }
}


