import { _decorator, Component, Node, Sprite } from 'cc';
import { ResMgr } from '../../Mgr/ResMgr';
const { ccclass, property } = _decorator;

@ccclass('SingleMenu')
/**
 * 
 */
export class SingleMenu extends Component {

    //
    @property(Sprite)
    private Cookie: Sprite | null = null;

    //
    @property(Sprite)
    private Food0: Sprite | null = null;

    //1
    @property(Sprite)
    private Food1: Sprite | null = null;

    //2
    @property(Sprite)
    private Food2: Sprite | null = null;


    /**
     * 
     * @param 
     * @param 
     * @param 
     * @param 
     */
    public UpdateContent(_cookie: string, _food0: string, _food1: string, _food2: string) {
        this.Cookie.spriteFrame = ResMgr.instance.SpriteFrames[_cookie];
        this.Food0.spriteFrame = ResMgr.instance.SpriteFrames[_food0];
        this.Food1.spriteFrame = ResMgr.instance.SpriteFrames[_food1];
        this.Food2.spriteFrame = ResMgr.instance.SpriteFrames[_food2];
    }

}


