import { _decorator, Component, Node } from 'cc';
import { BaseManager } from './BaseManager';
import { Message } from './Message';
import { Global } from '../Global';
const { ccclass, property } = _decorator;

@ccclass('MessageCenter')
export class MessageCenter extends Component {
    //
    static Managers: BaseManager[] = [];

    /**
      * 
      * @param 
      * @param 
      * @param 
      */
    static SendMessage(_type: number, _command: number, _content: any) {
        // return;
        let msg: Message = new Message(_type, _command, _content);
        Global.IsDebug && console.log("", msg, this.Managers, this.Managers)
        for (let manager of this.Managers) {
            manager.ReceiveMessage(msg);
        };
    }
}


