import { _decorator, Component, Label, Node, Vec3 } from 'cc';
import { AudioMgr } from '../../Mgr/AudioMgr';
import { LocalMgr } from '../../Mgr/LocalMgr';
import { MessageCenter } from '../../Message/MessageCenter';
import { MessageType } from '../../Message/Message';
import { AdLoad } from '../../Global';
import { TweenTool } from '../../Tools/TweenTools';
import { LoginScene } from '../LoginScene/LoginScene';
const { ccclass, property } = _decorator;

@ccclass('DownButton')
/**
 * 
 */
export class DownButton extends Component {

    //
    @property(Node)
    private LoginScene: Node | null = null;

    //
    @property(Node)
    private BuildScene: Node | null = null;

    //
    @property(Node)
    private ClearScene: Node | null = null;

    //
    private NowScene: Node | null = null;

    //
    @property(Node)
    private HomeButton: Node | null = null;

    //
    @property(Node)
    private CatButton: Node | null = null;

    //
    @property(Node)
    private BuyButton: Node | null = null;


    //
    @property(Label)
    private HeartText: Label | null = null;

    //
    @property(Label)
    private CatText: Label | null = null;

    protected start(): void {
        this.NowScene = this.LoginScene;
        this.UpdateScale();

        this.UpdateHeart();
        this.UpdateCoin();
    }

    /**
     * 
     */
    public HomeClick() {
        AudioMgr.instance.PlayButton();

        if (this.NowScene == this.BuildScene) return;

        let nowscene: Node = this.NowScene;
        TweenTool.SceneMove(nowscene, new Vec3(0, 0, 0), new Vec3(750, 0, 0), () => {
            nowscene.active = false;
        })

        MessageCenter.SendMessage(
            MessageType.Type_Build,
            MessageType.Build_Init,
            null
        )
        this.NowScene = this.BuildScene;
        this.UpdateScale();
    }

    /**
     * 
     */
    public CatClick() {
        AudioMgr.instance.PlayButton();

        if (this.NowScene == this.LoginScene) return;

        let move_pos: Vec3 = new Vec3(750, 0, 0);
        if (this.NowScene === this.BuildScene) move_pos = new Vec3(-750, 0, 0)
        let nowscene: Node = this.NowScene;
        TweenTool.SceneMove(nowscene, new Vec3(0, 0, 0), move_pos, () => {
            nowscene.active = false;
        });

        MessageCenter.SendMessage(
            MessageType.Type_Login,
            MessageType.Login_Init,
            this.NowScene === this.BuildScene ? 750 : -750
        )
        this.NowScene = this.LoginScene;
        this.UpdateScale();
    }

    /**
     * 
     */
    public BuyClick() {
        AudioMgr.instance.PlayButton();

        if (this.NowScene == this.ClearScene) return;

        if (LocalMgr.instance.LocalInfor.heartnum == 0) {
            MessageCenter.SendMessage(
                MessageType.Type_Top,
                MessageType.Top_ShowAd,
                {
                    loadname: AdLoad.Heart,
                    cb: () => {
                        LocalMgr.instance.AddHeart(1);
                        this.UpdateHeart();
                        MessageCenter.SendMessage(
                            MessageType.Type_Top,
                            MessageType.Top_ShowPopbox,
                            "1"
                        )
                    }
                }
            )
            return;
        }

        let nowscene: Node = this.NowScene;
        TweenTool.SceneMove(nowscene, new Vec3(0, 0, 0), new Vec3(-750, 0, 0), () => {
            nowscene.active = false;
        })
        MessageCenter.SendMessage(
            MessageType.Type_Clear,
            MessageType.Clear_Init,
            null
        );

        LocalMgr.instance.AddHeart(-1);
        this.NowScene = this.ClearScene;
        this.UpdateScale();

        this.UpdateHeart();
    }

    /**
     * 
     */
    private UpdateScale() {

        let buy_scale: number = this.NowScene == this.ClearScene ? 1.5 : 1;
        let cat_scale: number = this.NowScene == this.LoginScene ? 1.5 : 1;
        let home_scale: number = this.NowScene == this.BuildScene ? 1.5 : 1;

        this.BuyButton.setScale(buy_scale, buy_scale, buy_scale);
        this.CatButton.setScale(cat_scale, cat_scale, cat_scale);
        this.HomeButton.setScale(home_scale, home_scale, home_scale);
    }



    /**
     * 
     */
    public UpdateHeart() {
        this.HeartText.string = LocalMgr.instance.LocalInfor.heartnum.toString();
    }

    /**
     * 
     */
    public UpdateCoin() {
        this.CatText.string = LocalMgr.instance.LocalInfor.coinnum.toString();
    }

}


