declare var tt;
import { _decorator, Component, Node, Sprite } from 'cc';
import { AudioMgr } from '../../Mgr/AudioMgr';
import { MessageCenter } from '../../Message/MessageCenter';
import { MessageType } from '../../Message/Message';
import { LocalMgr } from '../../Mgr/LocalMgr';
import { ResMgr } from '../../Mgr/ResMgr';
import { Global } from '../../Global';
const { ccclass, property } = _decorator;

@ccclass('PopGift')
export class PopGift extends Component {
    @property(Node)
    private AwardButton: Node;
    start() {

    }

    update(deltaTime: number) {

    }

    //
    private IsAward: boolean = true;

    /**
     * 
     */
    public Show() {
        tt.checkScene({
            scene: "sidebar",
            success: (res) => {
                console.log("check scene success: ", res, Global.SceneString);
                // this.IsAward = res.isExist;
                //
                if (res.isExist && res.isExist == true) {
                    if (Global.SceneString) {
                        console.log("")
                        this.AwardButton.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames["button2"]
                    } else {
                        console.log("111")
                        this.AwardButton.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames["button1"]

                    }
                } else {
                    // this.AwardButton.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames["button1"]
                    this.AwardButton.active = false;
                }
            },
            fail: (res) => {
                console.log("check scene fail:", res);
                //
            }
        });
    }

    /**
     * 
     */
    public ReawrdClick() {
        console.log("", Global.SceneString)

        if (Global.SceneString) {//
            this.CloseClick();
            LocalMgr.instance.AddHeart(1);
            MessageCenter.SendMessage(
                MessageType.Type_Top,
                MessageType.Top_ShowPopbox,
                "1"
            )
            MessageCenter.SendMessage(
                MessageType.Type_Login,
                MessageType.Login_Init,
                null
            )

            this.AwardButton.active = false;
        } else {
            this.CloseClick();
            tt.navigateToScene({
                scene: "sidebar",
                success: (res) => {
                    console.log("navigate to scene success");
                    // 
                },
                fail: (res) => {
                    console.log("navigate to scene fail: ", res);
                    // 
                },
            });
        }
    }

    /**
     * 
     */
    private CloseClick() {
        AudioMgr.instance.PlayButton();
        MessageCenter.SendMessage(
            MessageType.Type_Top,
            MessageType.Top_HideAll,
            null
        )
    }
}


