import { _decorator, Component, Node, Vec2, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Tools')
export class Tools extends Component {

    /**
     * 
     * @param 
     * @param 
     * @param 
     */
    static CheckPoint(_clickpos: Vec3, _elepos: Vec3, _width: number, _height: number) {
        if (Math.abs(_clickpos.x - _elepos.x) <= _width / 2 &&
            Math.abs(_clickpos.y - _elepos.y) <= _height / 2) {
            return true;
        }
        return false;
    }

    /**
     * 
     * @param _firstpoint 
     * @param _secondpoint 
     */
    static getPointAngle(_firstpoint: { hor: number, ver: number }, _secondpoint: { hor: number, ver: number }) {
        return Math.atan2(_secondpoint.ver - _firstpoint.ver, _secondpoint.hor - _firstpoint.hor);
    }



    /**
     * (2d x,y)
     * @param _firstpoint 
     * @param _secondpoint 
     */
    static getPointDistance(_firstpoint: Vec2, _secondpoint: Vec2) {
        let hordis: number = _secondpoint.x - _firstpoint.x;
        let verdis: number = _secondpoint.y - _firstpoint.y;
        return Math.sqrt(hordis * hordis + verdis * verdis);
    }

    /**
     * 
     * @param
     * @param
     */
    static checkSameDay(_firsttime: Date, _secondtime: Date) {
        return (_firsttime.getFullYear() === _secondtime.getFullYear() &&
            _firsttime.getMonth() === _secondtime.getMonth() &&
            _firsttime.getDate() === _secondtime.getDate())
    }



}


