import { _decorator, Component, EventTouch, Input, Label, Node, Vec3 } from 'cc';
import { Cat } from './Cat';
import { Global } from '../../Global';
import { LocalMgr } from '../../Mgr/LocalMgr';
import { TweenTool } from '../../Tools/TweenTools';
const { ccclass, property } = _decorator;

@ccclass('CatLoad')
/**
 * 
 */
export class CatLoad extends Component {

    //
    @property(Node)
    private Cat0: Node | null = null;

    @property(Node)
    private Cat1: Node | null = null;

    @property(Node)
    private Cat2: Node | null = null;

    //
    @property(Node)
    private Coin: Node | null = null;

    //
    @property(Label)
    private CoinText: Label | null = null;

    //
    private CatRange: number = 1;
    //
    private CatBreed: string[] = ["Dachen"]
    // private CatBreed: string[] = ["Dachen", "Duola", "Zuoda", "Huibai", "Huihuang", "Nainiu"]

    //
    private MenuRange: number = 1;
    private MenuBreed: string[] = ["FishLine"]
    // private MenuBreed: string[] = ["BeefLine", "ChickenLine", "FishLine", "BeefCan", "ChickenCan", "FishCan", "Beefcookie", "Chickencookie", "Fishcookie"]

    start() {

        this.Coin.active = false;

        this.Cat0.active = this.Cat1.active = this.Cat2.active = false;
        this.CatUpdateFood(this.Cat0);
        this.CatUpdateFood(this.Cat1);
        this.CatUpdateFood(this.Cat2);
    }

    /**
     * 
     * @param 
     * @param 
     * @param 
     */
    public CatUpdateFood(_cat: Node, _catbreed: string = null, _menubreed: string = null) {

        this.CatBreed = LocalMgr.instance.LocalInfor.cat.concat();
        this.MenuBreed = LocalMgr.instance.LocalInfor.menu.concat();

        this.CatRange = this.CatBreed.length;
        this.MenuRange = this.MenuBreed.length;

        let menu_breed: string = this.MenuBreed[Math.floor(this.MenuRange * Math.random())];
        let cat_breed: string = this.CatBreed[Math.floor(this.CatRange * Math.random())]

        _cat.getComponent(Cat).DisplayCat(menu_breed, cat_breed);
    }

    /**
     * 
     * @param 
     */
    public AddCoin(_coinnum: number, _pox: number) {
        console.log("", _coinnum, _pox)
        this.Coin.active = true;
        this.CoinText.string = "+" + _coinnum.toString();
        this.Coin.setPosition(new Vec3(_pox, 290, 0));
        TweenTool.CoinAppear(this.Coin, null);
    }

    /**
     * 
     * 
     * 
     * 
     * 
     * 
     * 
     */



}


