import { _decorator, Animation, Component, easing, Node, Sprite, Tween, tween, TweenAction, Vec3 } from 'cc';
import { Global } from '../../Global';
import { ResMgr } from '../../Mgr/ResMgr';
const { ccclass, property } = _decorator;

@ccclass('SingleFood')
/**
 * 
 */
export class SingleFood extends Component {

    //
    @property(Node)
    private Food: Node | null = null;

    //
    @property(Node)
    private Box: Node | null = null;

    //
    @property(Node)
    public Line: Node | null = null;

    //
    private ScaleNum = { choose: new Vec3(1.2, 1.2, 1.2), normal: new Vec3(1, 1, 1) };

    //
    public IIndex: { hor: number, ver: number } = {
        hor: 0, ver: 0
    };

    //
    public FoodName: string = "";

    //
    public IsChoose: boolean = false;

    //
    public DownNum: number = 3;

    start() {
        this.node.setScale(Global.FoodScale);
        this.Line.active = false;
        this.Box.active = false;
    }

    /**
     * 
     * @param 
     */
    public UpdateFood(_foodname: string) {
        this.FoodName = _foodname;
        this.Food.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames[this.FoodName];
    }

    /**
     * 
     * @param 
     */
    public UpdateIndex(_hor: number, _ver: number) {
        this.IIndex.hor = _hor;
        this.IIndex.ver = _ver;
    }


    /**
     * 
     * @param 
     */
    public IamChoose() {
        this.IsChoose = true;
        tween(this.Food).tag(0).set({ scale: this.ScaleNum.normal }).
            to(0.1, { scale: new Vec3(1.5, 1.5, 1.5) }, { easing: 'backIn' }).
            to(0.1, { scale: this.ScaleNum.choose }, { easing: 'backOut' }).
            start();
        this.Box.active = false;
    }

    /**
     * 
     */
    public ImaNormal() {
        this.IsChoose = false;
        Tween.stopAllByTag(0)
        this.Food.setScale(this.ScaleNum.normal);
        this.Box.active = false;
        this.Line.active = false;
    }

    /**
     * 
     */
    public UpdateLine(_lineangle: number) {
        // this.Line.active = true;
        this.Line.angle = _lineangle;
    }

    /**
     * 
     */
    public ChangeLineParent(_node: Node, _pos: Vec3) {
        _node.addChild(this.Line);
        this.Line.setPosition(_pos);
    }

    /**
     * 
     */
    public UpdatePos() {
        this.ImaNormal();
        if (this.DownNum == 0) return;
        tween(this.Food).set({ position: new Vec3(0, this.DownNum * 178, 0) }).to(0.3, { position: new Vec3(0, 0, 0) }, { easing: easing.backOut }).start();
    }


}


