import { _decorator, Component, EventTouch, Input, instantiate, Label, Node, Prefab, UITransform, Vec2, Vec3 } from 'cc';
import { MessageCenter } from '../../Message/MessageCenter';
import { MessageType } from '../../Message/Message';
import { Global } from '../../Global';
import { SingleCat } from './SingleCat';
import { LocalMgr } from '../../Mgr/LocalMgr';
import { AudioMgr } from '../../Mgr/AudioMgr';
import { Tools } from '../../Tools/Tools';
const { ccclass, property } = _decorator;

@ccclass('PopCat')
export class PopCat extends Component {

    //
    @property(Prefab)
    private SingleCat: Prefab | null = null;

    //
    private CatArray: Node[] = [];

    @property(Node)
    private MainLoad: Node | null = null;

    //
    @property(Node)
    private ContentLoad: Node | null = null;

    //
    @property(Node)
    private Content: Node | null = null;

    //
    private ClickPos: Vec2 = new Vec2(0, 0);

    protected onLoad(): void {

    }

    /**
     * 
     * @param 
     */
    public AddSingle(_catbreed: string) {
        let singlecat: Node = instantiate(this.SingleCat);
        this.MainLoad.addChild(singlecat);
        singlecat.getComponent(SingleCat).UpdateInfor(_catbreed);
        this.CatArray.push(singlecat);
    }

    protected onEnable(): void {

        //
        if (Global.ChangeCat) {
            Global.ChangeCat = false;
            for (let i = 0; i < this.CatArray.length; i++) {
                if (this.CatArray[i].parent) {
                    this.CatArray[i].off(Input.EventType.TOUCH_START, this.CatStart, this);
                    this.CatArray[i].off(Input.EventType.TOUCH_END, this.CatEnd, this);
                    this.CatArray[i].off(Input.EventType.TOUCH_CANCEL, this.CatEnd, this);
                    this.CatArray[i].parent.removeChild(this.CatArray[i]);
                }
            }
            this.CatArray = [];

            for (let key of LocalMgr.instance.LocalInfor.cat) {
                this.AddSingle(key);
            }
        }

        this.MainLoad.setPosition(new Vec3(0, 260, 0))
        this.ContentLoad.on(Input.EventType.TOUCH_MOVE, this.TouchMove, this);
        this.Content.active = false;

        for (let i = 0; i < this.CatArray.length; i++) {
            this.CatArray[i].on(Input.EventType.TOUCH_START, this.CatStart, this);
            this.CatArray[i].on(Input.EventType.TOUCH_END, this.CatEnd, this);
            this.CatArray[i].on(Input.EventType.TOUCH_CANCEL, this.CatEnd, this);
        }

    }

    protected onDisable(): void {
        this.ContentLoad.off(Input.EventType.TOUCH_MOVE, this.TouchMove, this);

        for (let i = 0; i < this.CatArray.length; i++) {
            this.CatArray[i].off(Input.EventType.TOUCH_START, this.CatStart, this);
            this.CatArray[i].off(Input.EventType.TOUCH_END, this.CatEnd, this);
            this.CatArray[i].off(Input.EventType.TOUCH_CANCEL, this.CatEnd, this);
        }
    }

    /**
     * 
     */
    private CatStart(_e: EventTouch) {
        this.ClickPos = _e.getLocation();
    }

    /**
     * 
     */
    private CatEnd(_e: EventTouch) {
        if (Tools.getPointDistance(_e.getLocation(), this.ClickPos) <= 30) {
            AudioMgr.instance.PlayButton();
            this.Content.active = true;
            this.Content.getComponent(Label).string = _e.target.getComponent(SingleCat).Des;
        }
    }

    /**
     * 
     */
    private CloseClick() {
        AudioMgr.instance.PlayButton();
        MessageCenter.SendMessage(
            MessageType.Type_Top,
            MessageType.Top_HideAll,
            null
        )
    }

    /**
    * 
    */
    private TouchMove(_e: EventTouch) {
        if (this.MainLoad.getComponent(UITransform).height <= 480) return;

        let pos_y: number = this.MainLoad.getPosition().y + _e.getDeltaY();

        if (pos_y <= 260) pos_y = 260;
        if (pos_y >= this.MainLoad.getComponent(UITransform).height - 250 + 50) pos_y = this.MainLoad.getComponent(UITransform).height - 250 + 50

        this.MainLoad.setPosition(new Vec3(0, pos_y, 0))

    }

}


