import { _decorator, Component, Label, Node, Sprite, Vec3 } from 'cc';
import { ResMgr } from '../../Mgr/ResMgr';
const { ccclass, property } = _decorator;

@ccclass('SingleToy')
/**
 * 
 */
export class SingleToy extends Component {

    //
    @property(Node)
    private Toy: Node | null = null;

    //
    @property(Label)
    private CoinText: Label | null = null;

    //
    public MyType: string = "";

    //
    public Cb: Function = null;

    //
    public ToyInfor: { sprite: string, price: number } = null;

    //
    private ScaleNum = {
        CatHome: 0.5,
        CatPic: 0.5,
        CatBall: 1,
        CatBowl: 1,
        CatWallShi: 0.8
    }


    /**
     * 
     */
    public UpdateContent(_type: string, _toyinfor: { sprite: string, price: number }) {
        this.MyType = _type;
        this.ToyInfor = _toyinfor;

        this.Toy.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames[_toyinfor.sprite];
        this.Toy.setScale(new Vec3(this.ScaleNum[this.MyType], this.ScaleNum[this.MyType], this.ScaleNum[this.MyType]))
        this.CoinText.string = _toyinfor.price.toString();
    }

}


