import { _decorator, CCInteger, Component, instantiate, Node, Prefab, tween, UIOpacity, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('StarEffect')
/**
 * 
 */
export class StarEffect extends Component {

    //
    @property(Prefab)
    private Star: Prefab | null = null;

    //
    @property(CCInteger)
    private StarNum: number = 0;

    //
    private StarArray: Node[] = [];

    start() {

        for (let i = 0; i < this.StarNum; i++) {
            let star: Node = instantiate(this.Star);
            this.node.addChild(star);
            this.StarArray.push(star)
            star.setScale(0.2, 0.2, 0.2)
            star.active = false;
        }

    }

    /**
     * 
     * @param 
     * @param 
     */
    public AddStar(_parent: Node, _pos: Vec3) {
        _parent.addChild(this.node);
        this.node.setPosition(_pos);
        this.PlayStar();
    }


    /**
     * 
     */
    private PlayStar() {
        let dis_num: number = 50;
        for (let i = 0; i < this.StarArray.length; i++) {
            let angle_num: number = 360 / this.StarNum * i * Math.PI / 180;
            let posx: number = dis_num * Math.cos(angle_num);
            let posy: number = dis_num * Math.sin(angle_num);
            let tween_angle: number = angle_num <= Math.PI / 2 ? -720 : 720;
            this.StarArray[i].active = true;

            tween(this.StarArray[i]).
                set({ position: new Vec3(0, 0, 0), angle: 0 }).
                to(1, { position: new Vec3(posx, posy, 0), angle: tween_angle / 2 }, {
                    easing: 'backOut', onUpdate: (_target: Node, _ratio: number) => {
                        _target.getComponent(UIOpacity).opacity = 255 - _ratio * 255
                    }, onComplete: (_target: Node) => {
                        _target.active = false;
                    }
                }).
                start();

        }
    }

}


