import { _decorator, Component, Node, tween, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('test')
export class test extends Component {
    start() {

    }

    update(deltaTime: number) {

    }

    /**
     * dianji 
     */
    private Click() {

        for (let i = 1; i <= 8; i++) {
            let lable: Node = this.node.getChildByName("Label-00" + i.toString());
            let pos: Vec3 = (lable.getPosition().add(new Vec3(0, (i % 2 == 0) ? -50 : 50)))

            tween(lable).to(0.5, { position: pos }).start();
        }

    }

}


