import { _decorator, Component, Label, Node, Sprite } from 'cc';
import { Global } from '../../Global';
import { ResMgr } from '../../Mgr/ResMgr';
const { ccclass, property } = _decorator;

@ccclass('SingleCat')
/**
 * 
 */
export class SingleCat extends Component {

    //
    @property(Node)
    private Body: Node | null = null;

    //
    @property(Node)
    private LeftHand: Node | null = null;

    //
    @property(Node)
    private RightHand: Node | null = null;

    //
    @property(Label)
    private Nick: Label | null = null;
    public Des: string = "";
    //
    public Cb: Function = null;

    /**
    * 
    * @param 
    */
    public UpdateInfor(_breed: string) {
        let cat: string = Global.CatBreed[_breed].sprite;
        this.Body.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames[cat];
        this.LeftHand.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames[cat + "hand"];
        this.RightHand.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames[cat + "hand"];

        this.Nick.string = Global.CatBreed[_breed].name;
        this.Des = Global.CatBreed[_breed].dec;
    }

}


