import { _decorator, Component, Node, Vec3 } from 'cc';
import { MessageCenter } from '../../Message/MessageCenter';
import { MessageType } from '../../Message/Message';
import { TweenTool } from '../../Tools/TweenTools';
import { AdLoad, Global } from '../../Global';
import { LocalMgr } from '../../Mgr/LocalMgr';
import { WxTools } from '../../Tools/WxTools';
import { AdMgr } from '../../Mgr/AdMgr';
import { LoginScene } from './LoginScene';
import { AudioMgr } from '../../Mgr/AudioMgr';
const { ccclass, property } = _decorator;

@ccclass('ButtonLoad')
/**
 * 
 */
export class ButtonLoad extends Component {

    //
    @property(Node)
    private MuteButton: Node | null = null;


    /**
     * 
     */
    private FoodClick() {
        AudioMgr.instance.PlayButton();
        MessageCenter.SendMessage(
            MessageType.Type_Top,
            MessageType.Top_ShowMenu,
            null
        )
    }

    /**
     * 
     */
    private InstruClick() {
        AudioMgr.instance.PlayButton();
        MessageCenter.SendMessage(
            MessageType.Type_Top,
            MessageType.Top_ShowInstru,
            "rule0"
        )
    }

    /**
     * 
     */
    private CatClick() {
        AudioMgr.instance.PlayButton();
        MessageCenter.SendMessage(
            MessageType.Type_Top,
            MessageType.Top_ShowCat,
            null
        )
    }

    /**
     * 
     */
    private BuyClick() {

        AudioMgr.instance.PlayButton();
        if (LocalMgr.instance.LocalInfor.heartnum == 0) {
            MessageCenter.SendMessage(
                MessageType.Type_Top,
                MessageType.Top_ShowAd,
                {
                    loadname: AdLoad.Heart,
                    cb: () => {
                        LocalMgr.instance.AddHeart(1);
                        this.node.parent.getComponent(LoginScene).UpdateHeart();
                        MessageCenter.SendMessage(
                            MessageType.Type_Top,
                            MessageType.Top_ShowPopbox,
                            "1"
                        )
                    }
                }
            )
            return;
        }

        TweenTool.SceneMove(this.node.parent, new Vec3(0, 0, 0), new Vec3(-750, 0, 0), () => {
            this.node.parent.active = false;
        })
        MessageCenter.SendMessage(
            MessageType.Type_Clear,
            MessageType.Clear_Init,
            null
        );

        LocalMgr.instance.AddHeart(-1);

    }

    /**
     * 
     */
    private BuildClick() {
        AudioMgr.instance.PlayButton();
        TweenTool.SceneMove(this.node.parent, new Vec3(0, 0, 0), new Vec3(750, 0, 0), () => {
            this.node.parent.active = false;
        })

        MessageCenter.SendMessage(
            MessageType.Type_Build,
            MessageType.Build_Init,
            null
        )
    }

    /**
     * 
     */
    private MusicButton() {
        AudioMgr.instance.PlayButton();

        if (AudioMgr.instance.IsMute) {
            AudioMgr.instance.PlayBgSound("bg");
        } else {
            AudioMgr.instance.StopBgSound();
        }

        this.MuteButton.active = AudioMgr.instance.IsMute;

    }


}


