declare var wx;
import { _decorator, Component, Node, SubContextView } from 'cc';
import { Global } from '../Global';
const { ccclass, property } = _decorator;

@ccclass('WxTools')
/**
 * 
 */
export class WxTools extends Component {

    /**
     * 
     * @param key
     * @param value
     */
    static SetStorage(_key: string, _value: string) {
        wx.setStorageSync(_key, _value)
    }

    /**
     * 
     * @param key
     */
    static GetStorage(_key: string) {
        return wx.getStorageSync(_key) == "" ? null : JSON.parse(wx.getStorageSync(_key))
    }

    //
    static PicUrl: string[] = [
        "https://mmocgame.qpic.cn/wechatgame/ydzroiaGwsvgc5MQZNt4Ad1589CWCtia9SLibHS6RoMbHkl6vXr6WHY681SllUyG9mU/0",
        "https://mmocgame.qpic.cn/wechatgame/ydzroiaGwsvglqy3vZQJq3wSoqANUPHOIbxbjuQUPQvoA38rXibRG9e2CqyYFPoVUE/0"
    ]

    //
    static PicId: string[] = [
        "VzBknUPxQ0GEbe2UH0aYvg==",
        "h9AkiPdoRUy5mfVtXT4edw=="
    ]

    /**
     * 
     * @param 
     * @param 
     */
    static Share(_title: string, _picindex: number) {
        if (!Global.IsWx) return;

        wx.shareAppMessage({
            title: _title,
            imageUrlId: this.PicId[_picindex],
            imageUrl: this.PicUrl[_picindex]
        })
    }

    /**
      * 
      */
    static SetShare() {
        if (!Global.IsWx) return;
        let pic_index: number = Math.floor(this.PicUrl.length * Math.random())
        wx.onShareAppMessage(function () {
            return {
                title: "",
                imageUrlId: this.PicId[pic_index],
                imageUrl: this.PicUrl[pic_index]
            }
        }.bind(this))
    }

    /**
     * 
     */
    static ShareMenu() {
        if (!Global.IsWx) return;
        wx.showShareMenu({
            menus: ['shareAppMessage', 'shareTimeline']
        })
    }



    /**
     * 
     * @param 
     * @param  success error loading none
     * @param  
     */
    static ShowToast(_title: string, _icon: string, _duration: number = 2000) {
        wx.showToast({
            title: _title,
            icon: _icon,
            duration: _duration
        })
    }

    /**
     * 
     */
    static HideToast() {
        wx.hideToast()
    }


    /**
     * 
     * @param 
     * @param 
     * @param  content:editabletrue confirm true canceltrue
     * @param 
     * @param 
     * @param 
     * @param  4
     * @param 
     * @param 
     * @param 
     * @param 
     * @param 
     */
    static ShowModal(_title: string, _content: string, _successcb: Function, _failcb: Function, _completecb: Function,) {
        wx.showModal({
            title: '',
            content: '',
            showCancel: false,
            editable: true,
            success(res) {
                if (res.confirm) {
                    Global.IsDebug && console.log('')
                } else if (res.cancel) {
                    Global.IsDebug && console.log('')
                }
            }
        })
    }

    /**
     * loading
     * @param 
     * @param 
     * @param 
     * @param 
     * @param 
     */
    static ShowLoading(_title: string, _mask: boolean = true, _successcb: Function = null, _failcb: Function = null, _completecb: Function = null) {
        wx.showLoading({
            title: _title,
            mask: _mask,
            success: _successcb,
            fail: _failcb,
            complete: _completecb
        })
    }

    /**
     * loading
     */
    static HideLoading() {
        wx.hideLoading();
    }

    /**
     * 
     * @param 
     * @param  6
     * @param 
     * @param  tapIndex
     * @param   
     * @param 
     */
    static ShowActionSheet(_alerttext: string, _itemlist: string[], _itemcolor: string = "#000000", _successcb: Function = null, _failcb: Function = null, _completecb: Function = null) {
        wx.showActionSheet({
            alertText: _alerttext,
            itemList: _itemlist,
            itemColor: _itemcolor,
            success: _successcb,
            fail: _failcb,
            complete: _completecb
        })
    }

    /**
     * 
     * @param  heavymediumlight
     */
    static VibrateShort(_type: string) {
        wx.vibrateShort({
            type: _type
        })
    }

    /**
     * 
     */
    static VibrateLong() {
        wx.vibrateLong()
    }

    /**
     * 
     * @param 
     */
    static GetUserInfor(_successcb: Function) {
        //
        wx.getSetting({
            success(res) {
                if (res.authSetting['scope.userInfo']) {
                    //  getUserInfo 
                    wx.getUserInfo({
                        success: function (res) {
                            Global.IsDebug && console.log(res.userInfo)
                            _successcb(res.userInfo)
                        }
                    })
                } else {

                    //
                    let sysInfo = wx.getSystemInfoSync();
                    let screenW = sysInfo.screenWidth;
                    let screenH = sysInfo.screenHeight;

                    //  wx.createUserInfoButton 
                    let button = wx.createUserInfoButton({
                        type: 'text',
                        text: '',
                        style: {
                            left: 0,
                            top: 0,
                            width: screenW,
                            height: screenH,
                            lineHeight: 0,
                            backgroundColor: '',
                            color: '#ffffff',
                            textAlign: 'center',
                            fontSize: 16
                        }
                    })
                    button.onTap((res) => {
                        // 
                        Global.IsDebug && console.log(res)
                        _successcb(res.userInfo)
                        button.destroy()
                    })
                }
            }
        })
    }


}


