import { _decorator, Component, Node } from 'cc';
import { BaseLoad } from './BaseLoad';
import { Message, MessageType } from './Message';
import { MessageCenter } from './MessageCenter';
const { ccclass, property } = _decorator;

@ccclass('BaseManager')
export class BaseManager extends Component {

    //
    private ReceiveList: BaseLoad[] = [];

    //
    public MessageType: number;

    start() {
        this.MessageType = this.SetMessageType();
        MessageCenter.Managers.push(this);//
    }

    /**
     * 
     */
    SetMessageType() {
        return MessageType.Type_Login;
    }

    /**
     * 
     */
    RegisterReceiver(_receive: BaseLoad) {
        this.ReceiveList.push(_receive);
    }

    /**
     * 
     */
    ReceiveMessage(_msg: Message) {
        if (_msg.Type != this.MessageType) return;
        for (let receive of this.ReceiveList) {
            receive.ReceiveMessage(_msg);
        }
    }

}


