import { _decorator, Component, EventTouch, Input, instantiate, Node, Prefab, UITransform, Vec2, Vec3 } from 'cc';
import { MessageCenter } from '../../Message/MessageCenter';
import { MessageType } from '../../Message/Message';
import { AudioMgr } from '../../Mgr/AudioMgr';
import { SingleToy } from './SingleToy';
import { Tools } from '../../Tools/Tools';
import { LocalMgr } from '../../Mgr/LocalMgr';
const { ccclass, property } = _decorator;

@ccclass('PopToy')
export class PopToy extends Component {

    //
    @property(Prefab)
    private SingleToy: Prefab | null = null;

    //
    private ToyArray: { CatHome: Node[], CatPic: Node[], CatBall: Node[], CatBowl: Node[], CatWallShi: Node[] } = {
        CatHome: [],
        CatPic: [],
        CatBall: [],
        CatBowl: [],
        CatWallShi: []
    }

    //
    public BuildIndex: number = 0;

    //
    @property(Node)
    private MainLoad: Node | null = null;

    //
    @property(Node)
    private ContentLoad: Node | null = null;

    //
    private ToyInfor = {
        CatHome: [{ sprite: "cathome0", price: 50 }, { sprite: "cathome1", price: 50 }, { sprite: "cathome2", price: 50 }],
        CatPic: [{ sprite: "buildwindow0", price: 50 }, { sprite: "buildwindow1", price: 50 }, { sprite: "buildwindow2", price: 50 },],
        CatBall: [{ sprite: "catball0", price: 50 }, { sprite: "catball1", price: 50 }, { sprite: "catball2", price: 50 },],
        CatBowl: [{ sprite: "catbowl0", price: 50 }, { sprite: "catbowl1", price: 50 }, { sprite: "catbowl2", price: 50 },],
        CatWallShi: [{ sprite: "wallshipin0", price: 50 }, { sprite: "wallshipin1", price: 50 }, { sprite: "wallshipin2", price: 50 },]
    }

    //
    private ClickPos: Vec2 = new Vec2(0, 0);

    protected onLoad(): void {
        for (let key in this.ToyInfor) {
            for (let i = 0; i < this.ToyInfor[key].length; i++) {
                let st: Node = instantiate(this.SingleToy);
                this.MainLoad.getChildByName(key).addChild(st);
                st.getComponent(SingleToy).UpdateContent(key, this.ToyInfor[key][i])
                this.ToyArray[key].push(st);
            }
        }

    }

    protected onEnable(): void {
        this.MainLoad.setPosition(new Vec3(0, 250, 0))
        this.ContentLoad.on(Input.EventType.TOUCH_MOVE, this.TouchMove, this);

        for (let key in this.ToyArray) {
            for (let i = 0; i < this.ToyArray[key].length; i++) {
                this.ToyArray[key][i].on(Input.EventType.TOUCH_START, this.ToyStart, this);
                this.ToyArray[key][i].on(Input.EventType.TOUCH_END, this.ToyEnd, this);
                this.ToyArray[key][i].on(Input.EventType.TOUCH_CANCEL, this.ToyEnd, this);
            }
        }

    }

    protected onDisable(): void {
        this.ContentLoad.off(Input.EventType.TOUCH_MOVE, this.TouchMove, this);


        for (let key in this.ToyArray) {
            for (let i = 0; i < this.ToyArray[key].length; i++) {
                this.ToyArray[key][i].off(Input.EventType.TOUCH_START, this.ToyStart, this);
                this.ToyArray[key][i].off(Input.EventType.TOUCH_END, this.ToyEnd, this);
                this.ToyArray[key][i].off(Input.EventType.TOUCH_CANCEL, this.ToyEnd, this);
            }
        }


    }

    /**
     * 
     */
    private UpdateToy() {

        for (let key in this.ToyArray) {

            if (LocalMgr.instance.LocalInfor.toyinfor[this.BuildIndex][key] !== "") {
                for (let i = 0; i < this.ToyArray[key].length; i++) {
                    this.ToyInfor[key][i].price = 0;
                    this.ToyArray[key][i].getComponent(SingleToy).UpdateContent(key, this.ToyInfor[key][i])
                }
            }

        }

    }

    /**
     * 
     * @param 
     */
    public UpdateShow(_buildinfor: { type: string, index: number }) {
        this.UpdateToy();
        for (let key in this.ToyInfor) {
            this.MainLoad.getChildByName(key).active = (key == _buildinfor.type)
        }

        this.BuildIndex = _buildinfor.index;
    }

    /**
     * 
     */
    private ToyStart(_e: EventTouch) {
        this.ClickPos = _e.getLocation();
    }

    /**
     * 
     */
    private ToyEnd(_e: EventTouch) {
        if (Tools.getPointDistance(_e.getLocation(), this.ClickPos) <= 30) {
            this.BuyToy(_e.target);
        }
    }

    /**
     * 
     */
    private TouchMove(_e: EventTouch) {
        if (this.MainLoad.getComponent(UITransform).height <= 480) return;

        let pos_y: number = this.MainLoad.getPosition().y + _e.getDeltaY();

        if (pos_y <= 250) pos_y = 250;
        if (pos_y >= this.MainLoad.getComponent(UITransform).height - 250 + 50) pos_y = this.MainLoad.getComponent(UITransform).height - 250 + 50

        this.MainLoad.setPosition(new Vec3(0, pos_y, 0))
    }

    /**
    * 
    */
    private CloseClick() {
        AudioMgr.instance.PlayButton();
        MessageCenter.SendMessage(
            MessageType.Type_Top,
            MessageType.Top_HideAll,
            null
        )
    }

    /**
     * 
     * @param 
     * @param 
     * @param 
     */
    private BuyToy(_toy: Node) {
        AudioMgr.instance.PlayButton();

        //
        let toy_type: string = _toy.getComponent(SingleToy).MyType;
        //
        let toy_infor: { sprite: string, price: number } = _toy.getComponent(SingleToy).ToyInfor

        if (LocalMgr.instance.LocalInfor.coinnum >= toy_infor.price) {//
            MessageCenter.SendMessage(
                MessageType.Type_Build,
                MessageType.Build_Buy,
                {
                    buildindex: this.BuildIndex,
                    toyinfor:
                    {
                        type: toy_type,
                        sprite: toy_infor.sprite
                    }
                }
            );
            LocalMgr.instance.AddCoin(-toy_infor.price);
            LocalMgr.instance.LocalInfor.toyinfor[this.BuildIndex][toy_type] = toy_infor.sprite;
            this.UpdateToy();
            this.CloseClick();
        } else {//
            this.CloseClick();
            MessageCenter.SendMessage(
                MessageType.Type_Top,
                MessageType.Top_ShowPopbox,
                ""
            );
        }



    }
}


