import { _decorator, Component, Input, Node } from 'cc';
import { TopManager } from '../../Message/Manager/TopManager';
import { BaseLoad } from '../../Message/BaseLoad';
import { Message, MessageType } from '../../Message/Message';
import { Global } from '../../Global';
import { TweenTool } from '../../Tools/TweenTools';
import { PopBox } from './PopBox';
import { PopToy } from './PopToy';
import { PopRule } from './PopRule';
import { PopGet } from './PopGet';
import { PopCat } from './PopCat';
import { PopMenu } from './PopMenu';
import { PopAd } from './PopAd';
import { AdMgr } from '../../Mgr/AdMgr';
import { PopGift } from './PopGift';
const { ccclass, property } = _decorator;

@ccclass('TopScene')
/**
 * 
 */
export class TopScene extends BaseLoad {

    //
    @property(Node)
    private MaskBg: Node | null = null;

    //
    @property(Node)
    private PopMenu: Node | null = null;

    //
    @property(Node)
    private PopCat: Node | null = null;

    //
    @property(Node)
    private PopInstru: Node | null = null;

    //
    @property(Node)
    private PopToy: Node | null = null;

    //
    @property(Node)
    private PopBox: Node | null = null;

    //
    @property(Node)
    private PopGet: Node | null = null;

    //
    @property(Node)
    private PopAd: Node | null = null;
    //
    @property(Node)
    private PopGift: Node;

    protected onLoad(): void {

        TopManager._instance.RegisterReceiver(this);

        this.MaskBg.on(Input.EventType.TOUCH_START, () => { }, this);
        this.HideAll();
    }

    /**
     * 
     */
    ReceiveMessage(_msg: Message): void {
        Global.IsDebug && console.log("TopScene", _msg);
        switch (_msg.Command) {
            case MessageType.Top_HideAll:
                this.HideAll();
                break;
            case MessageType.Top_ShowMenu:
                this.ShowMenu();
                break;
            case MessageType.Top_ShowInstru:
                this.ShowInstru(_msg.Content);
                break;
            case MessageType.Top_ShowCat:
                this.ShowCat();
                break;
            case MessageType.Top_ShowPopbox:
                this.ShowPop(_msg.Content);
                break;
            case MessageType.Top_ShowToy:
                this.ShowToy(_msg.Content);
                break;
            case MessageType.Top_ShowGet:
                this.ShowGet(_msg.Content);
                break;
            case MessageType.Top_AddCat:
                // this.PopCat.getComponent(PopCat).AddSingle(_msg.Content);
                Global.ChangeCat = true;
                break;
            case MessageType.Top_AddMenu:
                // this.PopMenu.getComponent(PopMenu).AddSingle(_msg.Content);
                Global.ChangeMenu = true;
                break;
            case MessageType.Top_ShowAd:
                this.ShowAd(_msg.Content);
                break;
            case MessageType.Top_ShowGift:
                this.ShowGift();
                break;
        }
    }
    /**
     * 
     */
    public ShowGift() {
        this.MaskBg.active = true;
        this.PopGift.active = true;
        this.PopGift.getComponent(PopGift).Show();
    }


    /**
     * 
     */
    public HideAll() {
        for (let chi of this.node.children) chi.active = false;
        return;
    }

    /**
     * 
     */
    private ShowInstru(_rule: string) {
        this.MaskBg.active = true;
        this.PopInstru.active = true;
        this.PopInstru.getComponent(PopRule).UpdateContent(_rule)
        TweenTool.Pop(this.PopInstru);
    }

    /**
     * 
     */
    private ShowMenu() {
        this.MaskBg.active = true;
        this.PopMenu.active = true;
        TweenTool.Pop(this.PopMenu);
    }

    /**
     * 
     */
    private ShowCat() {
        this.MaskBg.active = true;
        this.PopCat.active = true;
        TweenTool.Pop(this.PopCat);
    }

    /**
     * 
     * @param 
     */
    private ShowPop(_content: string) {
        this.MaskBg.active = true;
        this.PopBox.getComponent(PopBox).ShowToast(_content);
    }

    /**
     * 
     * @param 
     */
    private ShowToy(_buildinfor: { type: string, index: number }) {
        this.MaskBg.active = true;
        this.PopToy.active = true;
        this.PopToy.getComponent(PopToy).UpdateShow(_buildinfor);
        TweenTool.Pop(this.PopToy);
    }

    /**
     * 
     * @param 
     */
    private ShowGet(_infor: { _iscat: boolean, _sprite: string }[]) {
        this.MaskBg.active = true;
        this.PopGet.active = true;
        this.PopGet.getComponent(PopGet).UpdateContent(_infor);
        TweenTool.Pop(this.PopGet);
    }

    /**
     * 
     */
    private ShowAd(_adinfor: { loadname: string, cb: Function }) {
        this.MaskBg.active = true;
        this.PopAd.active = true;
        this.PopAd.getComponent(PopAd).UpdateContent(_adinfor.loadname, _adinfor.cb);
        TweenTool.Pop(this.PopAd);
    }




}


