import { _decorator, Component, Node, Vec3 } from 'cc';
import { BaseLoad } from '../../Message/BaseLoad';
import { ClearManager } from '../../Message/Manager/ClearManager';
import { Message, MessageType } from '../../Message/Message';
import { Global } from '../../Global';
import { TweenTool } from '../../Tools/TweenTools';
import { MessageCenter } from '../../Message/MessageCenter';
import { ChooseScene } from './ChooseScene';
import { ControlLoad } from './ControlLoad';
import { AudioMgr } from '../../Mgr/AudioMgr';
import { LocalMgr } from '../../Mgr/LocalMgr';
const { ccclass, property } = _decorator;

@ccclass('ClearScene')
export class ClearScene extends BaseLoad {

    //
    @property(Node)
    private ChooseScene: Node | null = null;

    //
    @property(Node)
    private ControLoad: Node | null = null;

    //
    private IsClick: boolean = false;

    protected onLoad(): void {
        ClearManager._instance.RegisterReceiver(this)
        this.node.active = false;
    }

    /**
     * 
     */
    ReceiveMessage(_msg: Message): void {
        Global.IsDebug && console.log("ClearScene", _msg);
        switch (_msg.Command) {
            case MessageType.Clear_Init:
                this.Init();
                break;
        }
    }

    /**
     * 
     */
    private Init() {
        this.IsClick = false;
        this.node.active = true;
        TweenTool.SceneMove(this.node, new Vec3(750, 0, 0), new Vec3(0, 0, 0), () => {
            this.IsClick = true;

            if (!LocalMgr.instance.GetFirst("clear")) {
                MessageCenter.SendMessage(
                    MessageType.Type_Top,
                    MessageType.Top_ShowInstru,
                    "rule1"
                );
                LocalMgr.instance.UpdateFirst("clear");
            }

        });
        this.ChooseScene.getComponent(ChooseScene).Init();
        this.ControLoad.getComponent(ControlLoad).Init();
    }

    start() {

    }

    update(deltaTime: number) {

    }
}


