import { _decorator, Component, Label, Node, Vec3 } from 'cc';
import { BaseLoad } from '../../Message/BaseLoad';
import { LoginManager } from '../../Message/Manager/LoginManager';
import { Message, MessageType } from '../../Message/Message';
import { Global } from '../../Global';
import { MessageCenter } from '../../Message/MessageCenter';
import { TweenTool } from '../../Tools/TweenTools';
import { DrawLoad } from './DrawLoad';
import { LocalMgr } from '../../Mgr/LocalMgr';
import { DownButton } from '../DownButton/DownButton';
const { ccclass, property } = _decorator;

@ccclass('LoginScene')
export class LoginScene extends BaseLoad {

    //
    @property(Node)
    private DrawLoad: Node | null = null;

    //
    @property(Node)
    private CatLoad: Node | null = null;

    //
    @property(Node)
    private CatHand: Node | null = null;

    //
    @property(Node)
    private DownButton: Node | null = null;

    protected onLoad(): void {
        LoginManager._instance.RegisterReceiver(this);
    }

    /**
     * 
     */
    ReceiveMessage(_msg: Message): void {
        Global.IsDebug && console.log("LoginScene", _msg);
        switch (_msg.Command) {
            case MessageType.Login_Init:
                this.Init(_msg.Content);
                break;
        }
    }

    start() {
        //
        for (let key in LocalMgr.instance.LocalInfor.food) {
            this.DrawLoad.getComponent(DrawLoad).UpdateBox(key);
        }
        this.DrawLoad.getComponent(DrawLoad).InitCookie();

        if (!LocalMgr.instance.GetFirst("login")) {
            MessageCenter.SendMessage(
                MessageType.Type_Top,
                MessageType.Top_ShowInstru,
                "rule0"
            );
            LocalMgr.instance.UpdateFirst("login");
        }
    }

    update(deltaTime: number) {

    }

    /**
     * 
     * @param 
     */
    public Init(_initpos: number) {
        this.node.active = true;
        TweenTool.SceneMove(this.node, new Vec3(_initpos, 0, 0), new Vec3(0, 0, 0));
        //
        for (let key in LocalMgr.instance.LocalInfor.food) {
            this.DrawLoad.getComponent(DrawLoad).UpdateBox(key);
        }
        this.DrawLoad.getComponent(DrawLoad).InitCookie();
        this.DownButton.getComponent(DownButton).UpdateHeart();
    }

    public UpdateCoin() {
        this.DownButton.getComponent(DownButton).UpdateCoin();
    }

    public UpdateHeart() {
        this.DownButton.getComponent(DownButton).UpdateHeart();
    }

}


