import { _decorator, Component, Node } from 'cc';
import { AdMgr } from '../../Mgr/AdMgr';
import { AudioMgr } from '../../Mgr/AudioMgr';
import { MessageCenter } from '../../Message/MessageCenter';
import { MessageType } from '../../Message/Message';
const { ccclass, property } = _decorator;

@ccclass('PopAd')
/**
 * 
 */
export class PopAd extends Component {

    //
    @property(Node)
    private HeartLoad: Node | null = null;

    //
    @property(Node)
    private StepLoad: Node | null = null;

    //
    private Cb: Function = null;

    start() {

    }


    /**
     * 
     * @param 
     * @param 
     */
    public UpdateContent(_loadname: string, _cb: Function) {
        this.HeartLoad.active = false;
        this.StepLoad.active = false;

        this[_loadname].active = true;
        this.Cb = _cb;

        return;
    }

    /**
     * 
     */
    private LookClick() {
        // AudioMgr.instance.PlayButton();
        // this.Cb();
        // return;
        //
        AdMgr.instance.ShowVideo((_isended) => {
            if (_isended) {
                this.CloseClick()
                this.Cb && this.Cb();
            }
        });
    }

    /**
     * 
     */
    private CloseClick() {
        AudioMgr.instance.PlayButton();
        MessageCenter.SendMessage(
            MessageType.Type_Top,
            MessageType.Top_HideAll,
            null
        )
    }
}


