import { _decorator, Camera, Component, EventTouch, Graphics, Input, instantiate, Label, Node, Prefab, Vec3, view } from 'cc';
import { SingleFood } from './SingleFood';
import { ResMgr } from '../../Mgr/ResMgr';
import { AdLoad, Global } from '../../Global';
import { Tools } from '../../Tools/Tools';
import { ChooseScene } from './ChooseScene';
import { LocalMgr } from '../../Mgr/LocalMgr';
import { MessageCenter } from '../../Message/MessageCenter';
import { MessageType } from '../../Message/Message';
import { AudioMgr } from '../../Mgr/AudioMgr';
import { AdMgr } from '../../Mgr/AdMgr';
const { ccclass, property } = _decorator;

@ccclass('ControlLoad')
/**
 * 
 */
export class ControlLoad extends Component {

    //
    @property(Node)
    private LineLoad: Node | null = null;

    //
    @property(Prefab)
    private Food: Prefab | null = null;

    //
    @property(Node)
    private ChooseLoad: Node | null = null;

    //
    @property(Label)
    private SurpleText: Label | null = null;
    private SurpleNum: number = 10;

    //
    @property(Graphics)
    private Line: Graphics | null = null;

    //
    @property(Label)
    private BsetStep: Label | null = null;

    //
    private FoodArray: Node[][] = [];

    //
    private FoodNum: { hor: number, ver: number } = { hor: 5, ver: 6 };
    private DistanceNum: number = 150;

    //
    private ControlSize: { width: number, height: number } = {
        width: 650, height: 900
    }

    //
    private IsStart: boolean = false;
    //
    private LastFood: Node | null = null;
    //
    private DispalyFood: Node[] = [];

    //
    private FoodName: string[] = ["fish", "chicken", "beef"];
    // private FoodName: string[] = ["fish", "chicken", "beef", "egg", "grass"];

    start() {

        this.DistanceNum *= Global.FoodScale.x;

        for (let i = 0; i < this.FoodNum.hor; i++) {
            let foodarray: Node[] = [];
            for (let j = 0; j < this.FoodNum.ver; j++) {
                let food: Node = instantiate(this.Food);
                this.node.addChild(food);
                food.getComponent(SingleFood).UpdateFood(this.FoodName[Math.floor(Math.random() * this.FoodName.length)]);
                food.getComponent(SingleFood).UpdateIndex(i, j);
                food.setPosition(new Vec3(
                    -this.ControlSize.width / 2 + (i + 0.5) * this.DistanceNum,
                    -this.ControlSize.height / 2 + (j + 0.5) * this.DistanceNum, 
                    0))
                food.getComponent(SingleFood).ChangeLineParent(this.LineLoad, food.getPosition());
                foodarray.push(food);
            }
            this.FoodArray.push(foodarray);
        }

        //
        this.node.setPosition(this.node.getPosition().add(new Vec3((this.ControlSize.width - this.FoodNum.hor * this.DistanceNum) / 2, 0, 0)))

        this.node.on(Input.EventType.TOUCH_START, this.TouchStart, this);
        this.node.on(Input.EventType.TOUCH_MOVE, this.TouchMove, this);
        this.node.on(Input.EventType.TOUCH_END, this.TouchEnd, this);
        this.node.on(Input.EventType.TOUCH_CANCEL, this.TouchEnd, this);

    }

    /**
     * 
     */
    public Init() {
        this.SurpleNum = LocalMgr.instance.LocalInfor.stepnum;
        this.UpdateSurple();

        this.BsetStep.string = ":" + LocalMgr.instance.LocalInfor.linenum.toString();
    }

    /**
     * 
     */
    private AddClick() {
        AudioMgr.instance.PlayButton();

        MessageCenter.SendMessage(
            MessageType.Type_Top,
            MessageType.Top_ShowAd,
            {
                loadname: AdLoad.Step,
                cb: () => {
                    LocalMgr.instance.AddStep();
                    this.SurpleNum += 3;
                    this.UpdateSurple();
                    MessageCenter.SendMessage(
                        MessageType.Type_Top,
                        MessageType.Top_ShowPopbox,
                        "3"
                    )
                }
            }
        )
    }

    /**
     * 
     */
    private UpdateSurple() {
        this.SurpleText.string = ":" + this.SurpleNum.toString();
    }

    /**
     * 
     */
    private TouchStart(_e: EventTouch) {

        if (this.SurpleNum == 0) {
            this.AddClick();
            return;
        }

        //
        let click_pos: Vec3 = new Vec3(_e.getUILocation().x, _e.getUILocation().y, 0).subtract(this.node.getWorldPosition());

        this.LastFood = this.ReturnFood(click_pos);

        if (this.LastFood != null) {
            this.LastFood.getComponent(SingleFood).IamChoose();
            this.DispalyFood = [this.LastFood];
            this.Line.clear();
            this.Line.moveTo(this.LastFood.getPosition().x, this.LastFood.getPosition().y);
            AudioMgr.instance.PlayLine();
        }

    }

    /**
     * 
     */
    private TouchMove(_e: EventTouch) {

        //
        let click_pos: Vec3 = new Vec3(_e.getUILocation().x, _e.getUILocation().y, 0).subtract(this.node.getWorldPosition());

        let clickfood: Node = this.ReturnFood(click_pos);
        if (this.DispalyFood.length != 0 && clickfood != null) {

            //
            let last_index: { hor: number, ver: number } = this.LastFood.getComponent(SingleFood).IIndex;
            let last_name: string = this.LastFood.getComponent(SingleFood).FoodName;

            //
            let new_index: { hor: number, ver: number } = clickfood.getComponent(SingleFood).IIndex;
            let new_name: string = clickfood.getComponent(SingleFood).FoodName;

            if (last_name == new_name) {
                //
                if (last_index.hor == new_index.hor && Math.abs(last_index.ver - new_index.ver) == 1) {
                    this.UpdateChooseFood(Tools.getPointAngle(last_index, new_index), clickfood);
                }
                //
                if (last_index.ver == new_index.ver && Math.abs(last_index.hor - new_index.hor) == 1) {
                    this.UpdateChooseFood(Tools.getPointAngle(last_index, new_index), clickfood);
                }
                //
                if (Math.abs(last_index.ver - new_index.ver) == 1 && Math.abs(last_index.hor - new_index.hor) == 1) {
                    this.UpdateChooseFood(Tools.getPointAngle(last_index, new_index), clickfood);
                }
            }

        };
    }

    /**
     * 
     * @param 
     * @param 
     */
    private UpdateChooseFood(_lineangle: number, _clickfood: Node) {
        this.Line.lineTo(_clickfood.getPosition().x, _clickfood.getPosition().y);
        this.Line.stroke();
        AudioMgr.instance.PlayLine();
        this.LastFood.getComponent(SingleFood).UpdateLine(_lineangle * 180 / Math.PI - 90);
        this.LastFood = _clickfood;
        this.LastFood.getComponent(SingleFood).IamChoose();
        this.DispalyFood.push(this.LastFood)
    }

    /**
     * 
     */

    private TouchEnd(_e: EventTouch) {

        this.Line.clear();
        if (this.SurpleNum == 0) {
            return;
        }

        //
        let display_length: number = this.DispalyFood.length;

        //
        if (display_length > 1) {

            AudioMgr.instance.PlaySuccess();

            //
            for (let food of this.DispalyFood) {
                LocalMgr.instance.AddFood(food.getComponent(SingleFood).FoodName, 1);
                this.ChooseLoad.getComponent(ChooseScene).UpdateFood(food.getComponent(SingleFood).FoodName)
            }

            //
            let horarray: number[] = [];
            for (let display of this.DispalyFood) if (horarray.indexOf(display.getComponent(SingleFood).IIndex.hor) < 0) horarray.push(display.getComponent(SingleFood).IIndex.hor);

            for (let hor of horarray) {

                let dis_array: Node[] = [];
                let nodis_array: Node[] = [];
                let max_num: number = 0;

                let array_length: number = this.FoodArray[hor].length;
                for (let i = 0; i < array_length; i++) {
                    if (this.FoodArray[hor][i].getComponent(SingleFood).IsChoose) {
                        dis_array.push(this.FoodArray[hor][i]);
                        max_num++;
                    }
                    else {
                        nodis_array.push(this.FoodArray[hor][i]);
                        //
                        let down_num: number = 0;
                        for (let j = 0; j <= i; j++) {
                            if (this.FoodArray[hor][j].getComponent(SingleFood).IsChoose) down_num++;
                        };
                        this.FoodArray[hor][i].getComponent(SingleFood).DownNum = down_num;
                    }
                };

                for (let dis of dis_array) {

                    //  3   6  9
                    let menu: string[] = LocalMgr.instance.LocalInfor.menu;

                    this.FoodName = ["fish", "chicken", "beef", "grass"];
                    if (menu.length < 4) { this.FoodName = ["fish", "chicken", "beef"]; }
                    else if (menu.length > 6) { this.FoodName = ["fish", "chicken", "beef", "egg", "grass"]; }

                    dis.getComponent(SingleFood).DownNum = max_num;
                    dis.getComponent(SingleFood).UpdateFood(this.FoodName[Math.floor(Math.random() * this.FoodName.length)]);
                }

                this.FoodArray[hor] = nodis_array.concat(dis_array);

                for (let i = 0; i < this.FoodArray[hor].length; i++) {
                    this.FoodArray[hor][i].getComponent(SingleFood).IIndex = { hor: hor, ver: i };
                    this.FoodArray[hor][i].setPosition(new Vec3(-this.ControlSize.width / 2 + (hor + 0.5) * this.DistanceNum, -this.ControlSize.height / 2 + (i + 0.5) * this.DistanceNum, 0))
                    this.FoodArray[hor][i].getComponent(SingleFood).ChangeLineParent(this.LineLoad, this.FoodArray[hor][i].getPosition());
                    this.FoodArray[hor][i].getComponent(SingleFood).UpdatePos();
                }

            }

            this.SurpleNum--;
            this.UpdateSurple();

            this.BsetStep.string = ":" + Math.max(display_length, LocalMgr.instance.LocalInfor.linenum).toString();

            LocalMgr.instance.UpdateLine(display_length);

        } else {
            //
            for (let displayfood of this.DispalyFood) {
                displayfood.getComponent(SingleFood).ImaNormal();
            }
        }

        this.DispalyFood = [];
    }

    /**
     * 
     * @param 
     */
    public FoodDown(_food: Node) {

    }

    /**
     * 
     * @param 
     */
    private ReturnFood(_touchpos: Vec3): Node {
        let return_node = null;

        for (let i = 0; i < this.FoodArray.length; i++) {
            for (let j = 0; j < this.FoodArray[i].length; j++) {
                let food_pos: Vec3 = this.FoodArray[i][j].getPosition();

                //
                if (Tools.CheckPoint(food_pos, _touchpos, 110, 110) && this.DispalyFood.indexOf(this.FoodArray[i][j]) < 0) {
                    return this.FoodArray[i][j];
                }

            }
        };

        return return_node;
    }

}


