import { _decorator, Component, Node, Sprite } from 'cc';
import { TweenTool } from '../../Tools/TweenTools';
import { AudioMgr } from '../../Mgr/AudioMgr';
import { ResMgr } from '../../Mgr/ResMgr';
import { MessageCenter } from '../../Message/MessageCenter';
import { MessageType } from '../../Message/Message';
import { Global } from '../../Global';
const { ccclass, property } = _decorator;

@ccclass('PopGet')
/**
 * 
 */
export class PopGet extends Component {

    //
    @property(Node)
    private Light: Node | null = null;

    //
    @property(Node)
    private Title: Node | null = null;

    //
    @property(Node)
    private Content: Node | null = null;

    //
    private RotaSpeed: number = 2;

    private AllInfor: { _iscat: boolean, _sprite: string }[] = [];

    //
    private IsClick = false;

    start() {

    }

    update(deltaTime: number) {
        this.Light.angle += this.RotaSpeed;
    }

    /**
     * 
     * @param 
     */
    public UpdateContent(_infor: { _iscat: boolean, _sprite: string }[]) {
        this.AllInfor = _infor.concat();
        this.ShowContent()
    }

    /**
     * 
     */
    public ShowContent() {

        if (this.AllInfor.length == 0) {
            MessageCenter.SendMessage(
                MessageType.Type_Top,
                MessageType.Top_HideAll,
                null
            )
            return;
        }

        let is_cat: boolean = this.AllInfor[0]._iscat;

        if (is_cat) {
            //
            MessageCenter.SendMessage(
                MessageType.Type_Top,
                MessageType.Top_AddCat,
                this.AllInfor[0]._sprite
            )
            //
            MessageCenter.SendMessage(
                MessageType.Type_Build,
                MessageType.Build_AddBuild,
                null
            )
        } else {
            //
            MessageCenter.SendMessage(
                MessageType.Type_Top,
                MessageType.Top_AddMenu,
                this.AllInfor[0]._sprite
            )
        }

        this.IsClick = false;
        this.Title.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames[this.AllInfor[0]._iscat ? "miaotitle" : "congrtitle"];
        this.Content.getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames[this.AllInfor[0]._iscat ? Global.CatBreed[this.AllInfor[0]._sprite].sprite : this.AllInfor[0]._sprite];

        TweenTool.Pop(this.Content, () => {
            this.IsClick = true;
            this.AllInfor.splice(0, 1);
        })
    }

    /**
     * 
     */
    private CloseClick() {
        if (!this.IsClick) return;
        AudioMgr.instance.PlayButton();
        this.ShowContent();
    }

}


