import { _decorator, Component, Node } from 'cc';
import { Global } from '../Global';
import { WxTools } from '../Tools/WxTools';
import { MessageCenter } from '../Message/MessageCenter';
import { MessageType } from '../Message/Message';
const { ccclass, property } = _decorator;

@ccclass('LocalMgr')
/**
 * 
 */
export class LocalMgr extends Component {
    //
    private static _instance: LocalMgr = null;
    //
    public static get instance() {
        if (!this._instance) {
            this._instance = new LocalMgr();
        }
        return this._instance;
    }

    //Key
    private LocalKey: string = "LocalKey";

    //
    public SelfInfor = {};

    //
    public LocalInfor: {
        gamestart: number,
        heartnum: number,
        menu: string[],
        cat: string[],
        food: {},
        carecat: {
            Dachen: 0,
            Duola: 0,
            Zuoda: 0,
            Huibai: 0,
            Huihuang: 0,
            Nainiu: 0,
            Xiaoju: 0,
            Daju: 0,
            Xianluo: 0,
        },
        coinnum: number,
        failnum: number,
        toyinfor: { CatPic: string, CatHome: string, CatBall: string, CatBowl: string, CatWallShi: string }[],
        linenum: number,
        stepnum: number
    }

    //
    public AllInfor: {
        gamestart: number,
        heartnum: number,
        menu: string[],
        cat: string[],
        food: {

        },
        carecat: {
            Dachen: 0,
            Duola: 0,
            Zuoda: 0,
            Huibai: 0,
            Huihuang: 0,
            Nainiu: 0,
            Xiaoju: 0,
            Daju: 0,
            Xianluo: 0,
        },
        coinnum: number,
        failnum: number,
        toyinfor: { CatPic: string, CatHome: string, CatBall: string, CatBowl: string, CatWallShi: string }[],
        linenum: number,
        stepnum: number
    } = {
            gamestart: 0,
            heartnum: 3,
            menu: ["FishLine"],
            cat: ["Dachen",],
            food: {
                beef: 0,
                chicken: 0,
                grass: 0,
                fish: 3,
                egg: 0
            },
            carecat: {
                Dachen: 0,
                Duola: 0,
                Zuoda: 0,
                Huibai: 0,
                Huihuang: 0,
                Nainiu: 0,
                Xiaoju: 0,
                Daju: 0,
                Xianluo: 0,
            },
            coinnum: 0,
            failnum: 0,
            toyinfor: [
                { CatPic: "", CatHome: "", CatBall: "", CatBowl: "", CatWallShi: "" }
            ],
            linenum: 0,
            stepnum: 10
        }


    /**
     * 
     */
    public UpdateData() {
        if (Global.IsWx) {
            WxTools.SetStorage(this.LocalKey, JSON.stringify(this.LocalInfor));
            return;
        }

        localStorage.setItem(this.LocalKey, JSON.stringify(this.LocalInfor))

    }

    /**
     * 
     */
    public GetData() {
        if (Global.IsWx) {
            return WxTools.GetStorage(this.LocalKey)
        };
        return JSON.parse(localStorage.getItem(this.LocalKey));
    }

    private First: string = "first";

    /**
     * 
     */
    public GetFirst(_scene: string) {
        if (Global.IsWx) {
            return WxTools.GetStorage(_scene + this.First)
        };
        return JSON.parse(localStorage.getItem(_scene + this.First));
    }

    /**
     * 
     */
    public UpdateFirst(_scene: string) {
        if (Global.IsWx) {
            WxTools.SetStorage(_scene + this.First, "true");
            return;
        }

        localStorage.setItem(_scene + this.First, "true")
    }

    /**
     * 
     * @param 
     */
    public AddHeart(_heartnum: number) {
        this.LocalInfor.heartnum += _heartnum;
        this.UpdateData();
    }

    /**
     * 
     */
    public AddCoin(_coinnum: number) {
        this.LocalInfor.coinnum += _coinnum;
        this.UpdateData();
    }

    /**
     * 
     */
    public AddFail(_failnum: number) {
        this.LocalInfor.failnum += _failnum;
        this.UpdateData();
    }


    /**
     * 
     * @param 
     * @param 
     */
    public AddFood(_name: string, _addnum: number) {
        this.LocalInfor.food[_name] += _addnum;
        this.UpdateData();
    }

    //
    private CatLevel: number[] = [0, 10, 50, 100, 200, 300, 400, 500, 600]
    // private CatLevel: number[] = [0, 1, 2, 3, 4, 5, 6, 7, 8]
    private CatBreed: string[] = ["Dachen", "Duola", "Zuoda", "Huibai", "Huihuang", "Nainiu", "Xiaoju", "Daju", "Xianluo"];

    //
    private MenuLevel: number[] = [0, 1, 11, 31, 61, 101, 151, 211, 181]
    // private MenuLevel: number[] = [0, 1, 2, 3, 4, 5, 6, 7, 8]
    private MenuBreed: string[] = ["FishLine", "BeefLine", "ChickenLine", "FishCan", "BeefCan", "ChickenCan", "Fishcookie", "Beefcookie", "Chickencookie"];

    /**
     * 
     * @param 
     * @param 
     */
    public AddCat(_breed: string, _catnum: number = 1) {
        this.LocalInfor.carecat[_breed]++;
        this.UpdateData();

        let cat_num: number = 0;

        for (let key in this.LocalInfor.carecat) {
            cat_num += this.LocalInfor.carecat[key];
        };

        Global.IsDebug && console.log("", cat_num)

        let new_cat: { _iscat: boolean, _sprite: string }[] = [];

        //
        for (let i = 0; i < this.CatLevel.length; i++) {
            if (cat_num >= this.CatLevel[i] && this.LocalInfor.cat.indexOf(this.CatBreed[i]) < 0) {
                new_cat.push(
                    { _iscat: true, _sprite: this.CatBreed[i] }
                );
                this.AddCatBreed(this.CatBreed[i])
            }
        };


        //
        for (let i = 0; i < this.MenuLevel.length; i++) {
            if (cat_num >= this.MenuLevel[i] && this.LocalInfor.menu.indexOf(this.MenuBreed[i]) < 0) {
                new_cat.push(
                    { _iscat: false, _sprite: this.MenuBreed[i] }
                );
                this.AddMenu(this.MenuBreed[i])
            }
        };

        if (new_cat.length > 0) {
            MessageCenter.SendMessage(
                MessageType.Type_Top,
                MessageType.Top_ShowGet,
                new_cat
            )
        }

        Global.IsDebug && console.log("", new_cat)


    }

    /**
     * 
     */
    public AddMenu(_menuname: string) {
        this.LocalInfor.menu.push(_menuname);
        this.UpdateData();
    }

    /**
     * 
     */
    public AddCatBreed(_breed: string) {
        this.LocalInfor.cat.push(_breed);
        this.UpdateData();
    }

    /**
     * 
     * @param 
     */
    public AddBuild() {
        this.LocalInfor.toyinfor.push({ CatPic: "", CatHome: "", CatBall: "", CatBowl: "", CatWallShi: "" });
        this.UpdateData();
    }

    /**
     * 
     * @param 
     * @param 
     */
    public AddToy(_index: number, _content: { CatPic: string, CatHome: string, CatBall: string, CatBowl: string, CatWallShi: string }) {
        this.LocalInfor.toyinfor[_index] = _content;
        this.UpdateData();
    }

    /**
     * 
     */
    public UpdateLine(_linenum: number) {
        this.LocalInfor.linenum = Math.max(_linenum, this.LocalInfor.linenum);
        this.UpdateData();
    }

    /**
     * 
     */
    public AddStep() {
        this.LocalInfor.stepnum += 3;
        this.UpdateData();
    }

}


