declare var tt;
import { _decorator, Component, Node, RealInterpolationMode } from 'cc';
import { Global } from '../Global';
const { ccclass, property } = _decorator;

@ccclass('DyTools')
/**
 * 
 * 
 * 
 * 
 */
export class DyTools {

    /**
     * 
     * @param key
     * @param value   
     */
    static SetStorage(_key: string, _value: string) {
        tt.setStorageSync(_key, _value)
    }

    /**
     * 
     * @param key
     */
    static GetStorage(_key: string) {
        return tt.getStorageSync(_key) == "" ? null : JSON.parse(tt.getStorageSync(_key))
    }

    /**
     * 
     */
    static CanShareFriend() {
        if (!Global.IsDy) return;
        tt.showShareMenu({
            success(res) {
                console.log("");
            },
            fail(err) {
                console.log("showShareMenu ", err.errMsg);
            },
            complete(res) {
                console.log("showShareMenu ");
            },
        });
    }

}


