import { _decorator, Animation, Component, Node, Sprite, Vec3, VerticalTextAlignment } from 'cc';
import { MessageCenter } from '../../Message/MessageCenter';
import { MessageType } from '../../Message/Message';
import { ResMgr } from '../../Mgr/ResMgr';
import { AudioMgr } from '../../Mgr/AudioMgr';
import { LocalMgr } from '../../Mgr/LocalMgr';
import { StarEffect } from '../TopScene/StarEffect';
import { Global } from '../../Global';
const { ccclass, property } = _decorator;

@ccclass('SingleBuild')
/**
 * 
 */
export class SingleBuild extends Component {

    //
    @property(Node)
    private CatPic: Node | null = null;

    //
    @property(Node)
    private CatHome: Node | null = null;

    //
    @property(Node)
    private CatBall: Node | null = null;

    //
    @property(Node)
    private CatBowl: Node | null = null;

    //
    @property(Node)
    private CatWallShi: Node | null = null;

    //
    @property(Node)
    private Cat: Node | null = null;

    //
    private CatSpeed: number = 10;
    private CatVx: number = 0;

    //
    @property(Node)
    private StarEffect: Node | null = null;

    //
    public NowIndex: number = 0;

    //
    private BuildInfor: { CatPic: string, CatHome: string, CatBall: string, CatBowl: string, CatWallShi: string } = null;


    protected start(): void {
        this.CatSpeed = (1 + Math.floor(Math.random() * 2)) / 2;
        this.CatVx = this.CatSpeed;

        this.Cat.getChildByName("Cat").getComponent(Animation).play();
    }

    /**
     * 
     * @param 
     * @param 
     */
    public UdpateContent(_buildinfor: { CatPic: string, CatHome: string, CatBall: string, CatBowl: string, CatWallShi: string }, _index: number) {
        this.BuildInfor = _buildinfor;
        this.NowIndex = _index;
        for (let key in this.BuildInfor) {
            this[key].getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames[this.BuildInfor[key] == "" ? "littlejiabox" : this.BuildInfor[key]];
        }
    }

    /**
     * 
     * @param 
     */
    public UpdateCat(_breed: string) {
        let cat: string = Global.CatBreed[_breed].sprite;
        this.Cat.getChildByName("Cat").getChildByName("Cat").getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames[cat];
        this.Cat.getChildByName("Cat").getChildByName("LeftHand").getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames[cat + "hand"];
        this.Cat.getChildByName("Cat").getChildByName("RightHand").getComponent(Sprite).spriteFrame = ResMgr.instance.SpriteFrames[cat + "hand"];
    }

    protected update(dt: number): void {
        let move_pos: Vec3 = this.Cat.getPosition().add(new Vec3(this.CatVx, 0, 0));

        if (move_pos.x <= -300) this.CatVx = (1 + Math.floor(Math.random() * 2)) / 2;
        else if (move_pos.x >= 300) this.CatVx = -(1 + Math.floor(Math.random() * 2)) / 2;

        this.Cat.setPosition(move_pos)
    }

    /**
     * 
     * @param 
     * @param 
     */
    private ClickToy(_e, _cus) {
        Global.IsDebug && console.log("", _cus)
        AudioMgr.instance.PlayButton();
        MessageCenter.SendMessage(
            MessageType.Type_Top,
            MessageType.Top_ShowToy,
            {
                type: _cus,
                index: this.NowIndex
            }
        )


    }

    /**
     * 
     * @param 
     * @param 
     */
    public UdpateToy(_type: string, _spriteframe: string) {
        this.BuildInfor[_type] = _spriteframe;

        this.StarEffect.getComponent(StarEffect).AddStar(this.node, this[_type].getPosition());

        LocalMgr.instance.AddToy(this.NowIndex, this.BuildInfor)
        this.UdpateContent(this.BuildInfor, this.NowIndex);
    }


}


